
export default {
  data() {
    return {
      valid: true,
      name: "",
      nameRules: [(v) => !!v || this.$t("name")],
      email: "",
      emailRules: [
        (v) => !!v || this.$t("email"),
        (v) => /.+@.+\..+/.test(v) || this.$t("email"),
      ],
      text: "",
      starValue: 0,
      starRules: [(v) => !!v || this.$t("rating")],
      select: null,
      dialog: false,
      message: null,
    };
  },

  methods: {
    validate() {
      const valid = this.$refs.form.validate();
      if (valid) {
        this.valid = false;
        const mail = {
          toUids: ["sebastian"],
          ccUids: ["adrian", "mo", "paul"],
          from: this.email,
          replyTo: this.email,
          template: {
            name: "feedback",
            data: {
              name: this.name,
              text: this.text,
              rating: this.starValue,
              locale: this.$i18n.locale,
              email: this.email,
            },
            created: this.$fireModule.firestore.FieldValue.serverTimestamp(),
          },
        };
        this.$fire.firestore
          .collection("mail")
          .doc()
          .set(mail)
          .then(() => {
            this.message = this.$t("messageReceived");
          });
      }
    },
  },
};
